// const CREDITS = 'Model by DailyArt on Sketchfab';
const CREDITS = ' ';
const DESCRIPTION = 'Rendering deformable geometry with path tracing.';

let pathTracer, renderer, controls, camera, scene, clock;
let mixer, mixerAction;
let loader;
let counter = 0;
const params = {

	bounces: 5,
	samplesPerFrame: 1,
	renderScale: 1 / window.devicePixelRatio,
	tiles: 1,
	autoPause: true,
	// autoPause: false,
	pause: false,
	continuous: false,
	stableNoise: false,
	...getScaledSettings(),

};

const cameraParam = {
	// lookAtXDelta: 1,
	// lookAtYDelta: 1,
	posYDelta: 1
	// lookAtZDelta: 1
};

init();

async function init() {

	loader = new LoaderElement();
	loader.setDescription( DESCRIPTION );
	loader.attach( document.body );

	// renderer
	renderer = new WebGLRenderer( { antialias: true } );
	renderer.toneMapping = ACESFilmicToneMapping;
	document.body.appendChild( renderer.domElement );

	// path tracer
	pathTracer = new WebGLPathTracer( renderer );
	pathTracer.multipleImportanceSampling = false;
	pathTracer.tiles.set( params.tiles, params.tiles );
	pathTracer.filterGlossyFactor = 0.25;
	pathTracer.minSamples = 1;
	pathTracer.renderDelay = 0;
	pathTracer.fadeDuration = 0;

	// scene
	scene = new Scene();

	const oriCamPos = new Vector3( -5.5, 8.5, 7.5 );

	// camera
	camera = new PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.025, 500 );
	// camera.position.set( 5.5, 3.5, 7.5 );
	// camera.position.set( -5.5, 8.5, 7.5 );
	camera.position.set( oriCamPos.x, oriCamPos.y, oriCamPos.z );
	let curCamPos = oriCamPos;
	// controls
	controls = new OrbitControls( camera, renderer.domElement );
	camera.lookAt( controls.target );
	controls.addEventListener( 'change', () => pathTracer.updateCamera() );

	controls.update();

	// clock
	clock = new Clock();

	// load assets
	const modelUrl = window.location.hash === '#morphtarget' ? MORPH_URL : SKINNED_URL;
	const [ envTexture, gltf ] = await Promise.all( [
		new RGBELoader().loadAsync( ENV_URL ),
		new GLTFLoader().setMeshoptDecoder( MeshoptDecoder ).loadAsync( modelUrl )
	] );

	// update env map
	const generator = new BlurredEnvMapGenerator( renderer );
	const blurredTex = generator.generate( envTexture, 0.01 );
	scene.background = blurredTex;
	scene.environment = blurredTex;
	generator.dispose();

	// animations
	const animations = gltf.animations;
	mixer = new AnimationMixer( gltf.scene );

	mixerAction = mixer.clipAction( animations[ 0 ] );
	mixerAction.play();
	mixerAction.paused = params.pause;

	// initialize scene
	scene.add( gltf.scene );

// https://blog.csdn.net/weixin_48070278/article/details/132275988
	gltf.scene.scale.set(10, 10, 10);
	// gltf.scene.scale.set(3, 3, 3);

	let pos = gltf.scene.position;

	console.log("pos == " + pos.x + ", " + pos.y + ", " + pos.z);

	// const modelCenterBox = new THREE.Box3().setFromObject
	// gltf.scene.position.set(pos.x, pos.y - 10, pos.z);

	const floorTex = generateRadialFloorTexture( 2048 );
	const floorPlane = new Mesh(
		new PlaneGeometry(),
		new MeshStandardMaterial( {
			map: floorTex,
			transparent: true,
			color: 0xdddddd,
			roughness: 0.15,
			metalness: 1.0
		} )
	);
	floorPlane.scale.setScalar( 50 );
	floorPlane.rotation.x = - Math.PI / 2;
	floorPlane.position.y = 0.075;
	scene.add( floorPlane );

	// initial generation
	pathTracer.setScene( scene, camera );

	loader.setPercentage( 1 );
	loader.setCredits( CREDITS );

	onParamsChange();

	onResize();
	window.addEventListener( 'resize', onResize );

	// gui
	const gui = new GUI();
	gui.add( params, 'tiles', 1, 4, 1 ).onChange( value => {

		pathTracer.tiles.set( value, value );

	} );
	gui.add( params, 'bounces', 1, 10, 1 ).onChange( regenerateScene );
	gui.add( params, 'renderScale', 0.1, 1 ).onChange( v => {

		pathTracer.renderScale = v;
		pathTracer.reset();

	} );
	gui.add( params, 'autoPause' ).listen();
	gui.add( params, 'pause' ).onChange( v => {

		params.autoPause = false;
		setPause( v );

	} ).listen();
	gui.add( params, 'continuous' ).onChange( () => {

		params.autoPause = false;

	} );
	gui.add( params, 'stableNoise' ).onChange( v => {

		pathTracer.stableNoise = v;

	} );

	// gui.add(params, "C")



	// const cameraFolder = gui.addFolder( 'Camera' );
	// cameraFolder.add( camera, 'focusDistance', 1, 100 ).onChange( onParamsChange ).listen();
	// cameraFolder.add( camera, 'apertureBlades', 0, 10, 1 ).onChange( function ( v ) {

	// 	camera.apertureBlades = v === 0 ? 0 : Math.max( v, 3 );
	// 	this.updateDisplay();
	// 	onParamsChange();


	// } );
	// cameraFolder.add( camera, 'apertureRotation', 0, 12.5 ).onChange( onParamsChange );
	// cameraFolder.add( camera, 'anamorphicRatio', 0.1, 10.0 ).onChange( onParamsChange );
	// cameraFolder.add( camera, 'bokehSize', 0, 100 ).onChange( onParamsChange ).listen();
	// cameraFolder.add( camera, 'fStop', 0.02, 20 ).onChange( onParamsChange ).listen();
	// cameraFolder.add( camera, 'fStop', 0.02, 20 ).onChange( onParamsChange ).listen();
	// cameraFolder.add( camera, 'fov', 25, 100 ).onChange( () => {

	// 	camera.updateProjectionMatrix();
	// 	pathTracer.updateCamera();

	// } ).listen();

	let targetPos;

	// cameraFolder.add( camera, 'lookAtY', 25, 100 ).onChange( () => {

	// 	targetPos = new Vector3( controls.target.x, controls.target.y + 5, controls.target.z );
	// 	camera.lookAt( targetPos );
	// 	camera.updateProjectionMatrix();
	// 	pathTracer.updateCamera();

	// } ).listen();

	// cameraFolder.add( cameraParam, 'lookAtYDelta', - 20, 20 ).onChange( () => {
	// gui.add( cameraParam, 'lookAtYDelta', - 5, 5 ).onChange( value => {

	// 	targetPos = new Vector3( controls.target.x, controls.target.y + value, controls.target.z );
	// 	// controls.target = targetPos;
	// 	camera.lookAt( targetPos );
	// 	camera.updateProjectionMatrix();
	// 	pathTracer.updateCamera();
	// 	// controls.update();

	// } ).listen();

	// gui.add( cameraParam, 'posYDelta', - 1, 1 ).onChange( value => {

	// 	// targetPos = new Vector3( controls.target.x, controls.target.y + value, controls.target.z );
	// 	// controls.target = targetPos;
	// 	// camera.lookAt( targetPos );
	// 	curCamPos = camera.position;
	// 	camera.position.set( curCamPos.x, value, curCamPos.z );
	// 	camera.updateProjectionMatrix();
	// 	pathTracer.updateCamera();
	// 	// controls.update();

	// } ).listen();

	// controls.addEventListener( 'change', () => updateGuiCamPos( gui, camera.position.y ) );

	// cameraFolder.add( params, 'autoFocus' );

	animate();

}

// function updateGuiCamPos( gui, v ) {

// 	gui.cameraParam.controls. = v;

// }

function setPause( v ) {

	mixerAction.paused = v;
	params.pause = v;
	if ( v ) {

		regenerateScene();

	}

}

function onResize() {

	renderer.setSize( window.innerWidth, window.innerHeight );
	renderer.setPixelRatio( window.devicePixelRatio );

	camera.aspect = window.innerWidth / window.innerHeight;
	camera.updateProjectionMatrix();

	pathTracer.updateCamera();

}

function regenerateScene() {

	pathTracer.bounces = params.bounces;
	pathTracer.setScene( scene, camera );

}

function onParamsChange() {

	pathTracer.filterGlossyFactor = params.filterGlossyFactor;
	pathTracer.bounces = params.bounces;
	pathTracer.renderScale = params.renderScale;

	pathTracer.updateCamera();
	pathTracer.reset();

}

function animate() {

	requestAnimationFrame( animate );

	// step the animation forward
	const delta = Math.min( clock.getDelta(), 30 * 0.001 );
	mixer.update( delta );

	if ( params.autoPause ) {

		// auto pause the animation
		counter += delta;
		// if ( ! params.pause && counter >= 2.5 || params.pause && counter >= 5 ) {
		if ( ! params.pause && counter >= 8 || params.pause && counter >= 30 ) {

			setPause( ! params.pause );
			counter = 0;

		}

	} else {

		counter = 0;

	}

	pathTracer.dynamicLowRes = params.continuous;

	if ( ! params.pause && ! params.continuous ) {

		renderer.render( scene, camera );
		loader.setSamples( 0, pathTracer.isCompiling );

	} else {

		// if we're continuously path tracing then update the scene
		if ( ! params.pause && params.continuous ) {

			regenerateScene();

		}

		pathTracer.renderSample();
		loader.setSamples( pathTracer.samples, pathTracer.isCompiling );

	}

}
